
footer.flex{
   justify-content: space-between;
   padding-bottom: 1.9rem;
}
footer.flex ul{
    gap: 1rem;
}
footer.flex ul a {
    color: var(--subtitle);
    font-size: 0.9rem;
    font-weight: 400;
   
}
footer.flex ul a:hover {
    color: var(--blue);
    font-size: 1rem;
    font-weight: 500;
}

footer.flex p{
    margin-top: 1rem;
    color: rgb(113, 113,122)
}

footer.flex p a {
    color: var(--blue);
}


@media (max-width: 1000px) {
    
footer.flex{
    flex-direction: column;
    gap:2rem
}

footer.flex {
display :inline-block;
}

}