
/* Main design  */
main.flex{
    gap: 2rem;
    align-items: start;
}

/* left-section */

main section.left-section.flex{
    flex-direction: column;
    gap: 10px;
    
}

main section.left-section.flex button{
    background-color: rgba(36, 37, 46, 1);
    width: 11rem;
    padding: 0.75rem 0; 
    text-align: center;
    font-size: 1.05rem;
    text-transform: capitalize;
    opacity: 0.5;
    border-radius: 5px;
    transition: 0.3s;

}
main section.left-section.flex button:hover{
    opacity: 1;
}

main section.left-section.flex button.active{
    opacity: 1;
    font-weight: bold;
    letter-spacing: 0.6px;
    padding: 0.8rem 0;
    border: 1px solid var(--blue);

}
.light main section.left-section.flex button.active{
    border: 1px solid #000;
}


/* right-section */
main section.right-section.flex{
    /* flex-grow: 1; */
    flex-wrap: wrap;
    justify-content: center;
    /* gap: 16px; */
    column-gap: 16px;
    row-gap: 2rem;


}
 .card{
    box-shadow: 1px 1px 1px rgba(0,0,0,0.16),
    1px 1px 1px rgba(0,0,0,0.15);
    border: 1px solid rgba(93, 188, 252, 0.3);
    border-radius: 5px;
    transition: 0.3s;
    background-image: linear-gradient(
        rgba(255, 255, 255, 0.05),
        rgba(255, 255, 255, 0.05)
    );
 }
 .light .card , .light .card:hover{
    border: none;
}
 .card:hover{
    border: 1px solid var(--blue);
    rotate: 1deg;
    scale: 1.01;
    cursor: pointer;
    background-color: rgba(248, 248, 246);
    color: #000;
 }

 .card img{
    border-radius: 5px;
 }
 
.card .box{
    padding: 1rem 1.4rem;
}
.card .box .title{
    color: var(--title);
    text-transform: capitalize;
    text-align: justify;
}

.card .box .sub-title{
    color: var(--subtitle);
    font-size: 0.8rem;
    margin-top: 0.7rem;
    margin-bottom: 1.1rem;
    text-align: justify;
}


.card .box .icons{
    justify-content: space-between;

}
.card .icon-github-square, .card .icon-link{
    font-size: 1.2rem;
    color: var(--icon-hover);
    cursor: pointer;
}

.card a.link{
    font-size: 0.9rem;
    color: var(--blue);
    margin-right: 12px;

}

@media (max-width: 600px) {
    main.flex{
        flex-direction: column;
        gap: 2rem;
        align-items: center;
    }
    main section.left-section.flex{
        flex-direction: row;
        flex-wrap: wrap;
        justify-content: center;
        gap: 10px;
        
    }
    main section.left-section.flex button{
        width: 7rem;
        padding: 0.8rem 0; 
        font-size: 0.9rem;
    }
   }
